.products-section {
    display: flex;

    &--tablet {
        display: none;
    }

    &__row {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        
    }
}

.product-card {
    border-radius: 5px;
    overflow: hidden;
    transition: box-shadow 0.3s ease, transform 0.3s ease;
    &:hover {
        box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
        transform: translateY(-10px); 
      }


    &--full {
        margin-left: 158px;
    }

    &--wide {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        flex-wrap: wrap;
        margin-bottom: 20px;
    }

    &--standard {
        width: 184px;
        height: 328px;
        background-color: white;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    }

    &__image-wrapper {
        position: relative;
        background: url(../images/products/peas.jpeg) center center/cover no-repeat;
        width: 387px;
        height: 328px;
        &:hover {
            height: 348px;
        }
        
        
        &--standart {
            position: relative;
        }

        &--wide-one {
            position: relative;
            background: url(../images/products/blueberry.jpeg) center center/cover no-repeat;
            width: 387px;
            height: 154px;
        }

        &--wide-two {
            position: relative;
            background: url(../images/products/carrots.jpeg) center center/cover no-repeat;
            width: 387px;
            height: 154px;
        }

    }

    &__image {
        width: 100%;
        height: 100%;
        object-fit: cover;

        &--full {
            width: 387px;
            height: 328px;
        }

        &--standard {
            width: 184px;
            height: 183px;
        }
    }

    &__labels {
        position: absolute;
        top: 10px;
        left: 10px;
        display: flex;
        flex-direction: column;
        gap: 5px;

        &--wide {
            position: absolute;
            bottom: 12px;
            left: 12px;
        }
    }

    &__title {
        font-size: 20px;
        &--standart {
            width: 160px;
            font-size: 14px;
            margin-left: 12px;
            
        }
    }

  

  
    
    &__price-container {
        display: flex;
        position: absolute;
        right: 12px;
        bottom: 1px;

        &--standart {
            display: flex;
            flex-direction: column;
            align-items: center; 
        }


    }

    &__text-container {
        margin-left: 12px;
        &--full {
            position: absolute;
        }
    }

    &__price {
        color: white;
        &--standart {
            color: black; 
            margin: 8px;
        }


        &.product-card__price--full {
            position: absolute;
            width: 50px;
            height: 20px;
            top: 397%;
            left: 90%;
        }

        &.product-card__price--wide-one {
            width: 57px;
            height: 20px;
            margin-top: 10px;
        }

        &.product-card__price--wide-two {
            margin-top: 10px;
        }



    }

}

.icon-container {
    color: white;
    display: flex;
    flex-direction: column;
    position: absolute;
    justify-content: center;
    align-items: end;
    align-self: normal;
    position: absolute;
    right: 14px;
    top: 14px;
    i {
        transition: color 0.3s ease, transform 0.3s ease;
        &:hover {
            color: #ffeb3b; 
            transform: rotate(20deg);
        }
    }
}