@charset "UTF-8";
* {
  font-family: "Inter Bold";
}

@font-face {
  font-family: "Inter Bold";
  src: url("../fonts/inter_bold.woff2") format("woff2"), url("../fonts/inter_bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}
.text-white {
  color: #fff;
}

.video {
  min-height: 645px;
  padding: 0px;
}
.video__wrapper {
  position: relative;
  width: 1200px;
  height: 517px;
  margin: 0 auto;
  background: url(../images/video/video_preview.jpeg) center center/cover no-repeat;
}
.video__button {
  position: absolute;
  width: 180px;
  height: 33px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  clip-path: polygon(7% 0, 100% 0, 92% 100%, 0% 100%);
  background: #18c576;
  color: white;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 2; /* Кнопка будет поверх видео */
  transition: background-color 0.3s ease, transform 0.3s ease;
}
.video__button:hover {
  background-color: #16a463;
}
.video__text {
  margin-right: 10px;
  margin-bottom: 1px;
}
.video__icon i {
  font-size: 19px;
  color: white;
  vertical-align: middle;
}
.video__player {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
  z-index: 1;
}
.video__player iframe {
  width: 100%;
  height: 100%;
  border: none;
}

.products-section {
  display: flex;
}
.products-section--tablet {
  display: none;
}
.products-section__row {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.product-card {
  border-radius: 5px;
  overflow: hidden;
  transition: box-shadow 0.3s ease, transform 0.3s ease;
}
.product-card:hover {
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  transform: translateY(-10px);
}
.product-card--full {
  margin-left: 158px;
}
.product-card--wide {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin-bottom: 20px;
}
.product-card--standard {
  width: 184px;
  height: 328px;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}
.product-card__image-wrapper {
  position: relative;
  background: url(../images/products/peas.jpeg) center center/cover no-repeat;
  width: 387px;
  height: 328px;
}
.product-card__image-wrapper:hover {
  height: 348px;
}
.product-card__image-wrapper--standart {
  position: relative;
}
.product-card__image-wrapper--wide-one {
  position: relative;
  background: url(../images/products/blueberry.jpeg) center center/cover no-repeat;
  width: 387px;
  height: 154px;
}
.product-card__image-wrapper--wide-two {
  position: relative;
  background: url(../images/products/carrots.jpeg) center center/cover no-repeat;
  width: 387px;
  height: 154px;
}
.product-card__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.product-card__image--full {
  width: 387px;
  height: 328px;
}
.product-card__image--standard {
  width: 184px;
  height: 183px;
}
.product-card__labels {
  position: absolute;
  top: 10px;
  left: 10px;
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.product-card__labels--wide {
  position: absolute;
  bottom: 12px;
  left: 12px;
}
.product-card__title {
  font-size: 20px;
}
.product-card__title--standart {
  width: 160px;
  font-size: 14px;
  margin-left: 12px;
}
.product-card__price-container {
  display: flex;
  position: absolute;
  right: 12px;
  bottom: 1px;
}
.product-card__price-container--standart {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.product-card__text-container {
  margin-left: 12px;
}
.product-card__text-container--full {
  position: absolute;
}
.product-card__price {
  color: white;
}
.product-card__price--standart {
  color: black;
  margin: 8px;
}
.product-card__price.product-card__price--full {
  position: absolute;
  width: 50px;
  height: 20px;
  top: 397%;
  left: 90%;
}
.product-card__price.product-card__price--wide-one {
  width: 57px;
  height: 20px;
  margin-top: 10px;
}
.product-card__price.product-card__price--wide-two {
  margin-top: 10px;
}

.icon-container {
  color: white;
  display: flex;
  flex-direction: column;
  position: absolute;
  justify-content: center;
  align-items: end;
  align-self: normal;
  position: absolute;
  right: 14px;
  top: 14px;
}
.icon-container i {
  transition: color 0.3s ease, transform 0.3s ease;
}
.icon-container i:hover {
  color: #ffeb3b;
  transform: rotate(20deg);
}

.product-card__label {
  display: block;
  padding-top: 10px;
  border-radius: 50px;
  font-size: 12px;
  color: white;
  text-align: center;
}
.product-card__label-container--full {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 359%;
}
.product-card__label--discount {
  height: 23px;
  width: 55px;
  border-radius: 50px;
  text-align: center;
  padding-top: 10px;
}
.product-card__label--discount.product-card__label--discount-full {
  margin-bottom: 4px;
  background-color: rgb(24, 197, 118);
}
.product-card__label--discount.product-card__label--discount-wide {
  display: block;
  background-color: rgb(255, 91, 198);
  width: 55px;
  height: 23px;
  margin-bottom: 4px;
}
.product-card__label--discount.product-card__label--discount-standard {
  background-color: rgb(24, 197, 118);
}
.product-card__label--day.product-card__label--day {
  width: 83px;
  height: 23px;
  background-color: rgb(255, 167, 38);
}
.product-card__label--hit.product-card__label--hit {
  width: 58px;
  height: 23px;
  background-color: rgb(172, 89, 241);
}
.product-card__label--sale {
  height: 23px;
  width: 75px;
  border-radius: 50px;
  text-align: center;
  padding-top: 10px;
}
.product-card__label--sale.product-card__label--sale-wide {
  width: 98px;
  height: 23px;
  background-color: rgb(57, 176, 244);
}

.button {
  width: 142px;
  height: 32px;
  border-radius: 5px;
  background: #18c576;
  border: none;
  color: white;
  transition: background-color 0.3s ease, transform 0.3s ease;
}
.button:hover {
  background-color: #ff6f91;
  transform: scale(1.05);
}
.button--full {
  position: absolute;
  left: 114%;
  top: 410%;
}
.button--wide {
  margin-left: 8px;
}
.button--standart {
  width: 160px;
  height: 32px;
  margin-bottom: 12px;
}
.button--modal {
  position: absolute;
  right: 13px;
  width: 106px;
  height: 32px;
  bottom: 12px;
}

.modal {
  display: none;
  /* Скрыто по умолчанию */
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  justify-content: center;
  align-items: center;
  z-index: 1000;
}
.modal--open {
  display: flex;
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.modal__title {
  margin: 0;
  color: rgb(68, 68, 68);
}
.modal__content {
  width: 387px;
  height: 200px;
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  z-index: 1001;
  position: relative;
  min-width: 300px;
}
.modal__input {
  width: 378px;
  height: 34px;
  margin-top: 24px;
  padding-left: 12px;
  border-radius: 5px;
  box-shadow: none;
  outline: none;
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.modal__consent-label {
  display: block;
  margin-bottom: -7px;
}
.modal__consent-text {
  font-size: 8px;
  color: rgb(68, 68, 68);
}
.modal__consent-checkbox {
  margin: 0;
  width: 8px;
  height: 8px;
}
.modal__close {
  position: absolute;
  width: 10px;
  height: 10px;
  top: 8px;
  right: 30px;
  background: none;
  border: none;
  font-size: 1.9rem;
  cursor: pointer;
}

.error-message {
  color: rgb(228, 75, 75);
  font-size: 8px;
  margin-top: 5px;
}

.products-slider {
  display: none;
}

@media (max-width: 991px) {
  .video {
    padding: 64px;
    min-height: 0;
  }
  .video__wrapper {
    height: 337px;
    width: 100%;
  }
  .products-section {
    display: none;
  }
  .products-section--tablet {
    display: block;
  }
  .products-section__container--tablet {
    display: flex;
    margin-top: 19px;
  }
  .product-card__image-wrapper--standart.product-card__image-wrapper--tablet {
    height: 181px;
    width: 181px;
  }
  .product-card.product-card--full.product-card--tablet {
    width: 382px;
    height: 328px;
    margin-left: 57px;
  }
  .product-card__title.product-card__title--tablet {
    margin: 0;
  }
  .product-card__title.product-card__title--tablet.product-card__title--tablet--standart {
    margin-top: 14px;
    font-size: 14px;
    margin-left: 10px;
  }
  .product-card__price.product-card__price--tablet.product-card__price--tablet--standart {
    color: black;
  }
  .button.button--standart.button--standart--tablet {
    width: 157px;
    height: 32px;
    margin-right: 12px;
  }
  .product-card__price-container--standart.product-card__price-container--standart--tablet {
    margin-top: 28px;
  }
  .product-card__price.product-card__price--standart.product-card__price--standart--tablet {
    margin: 0;
    margin-right: 12px;
    margin-bottom: 8px;
  }
  .button.button--wide.button--wide--tablet {
    margin-left: 9px;
    margin-right: 18px;
  }
  .product-card--full {
    margin: 0;
  }
}
@media (max-width: 575px) {
  .video {
    margin-bottom: 48px;
    min-height: 0;
    padding: 0;
  }
  .video__wrapper {
    width: 328px;
    /* Исправляем на 100%, чтобы контент заполнил весь контейнер */
    height: 204px;
  }
  .products-section {
    display: block;
  }
  .products-section--tablet {
    display: none;
  }
  .products-section__row {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
    /* Центрируем карточки */
  }
  .product-card {
    width: 100%;
    /* Чтобы карточка занимала всю ширину */
    max-width: none;
  }
  .product-card:hover {
    box-shadow: none;
    transform: none;
  }
  .product-card--full,
  .product-card--wide {
    width: 100%;
  }
  .product-card__image-wrapper {
    width: 100%;
    /* Карточки заполняют всю ширину контейнера */
    height: 328px;
    background-size: cover;
    /* Обеспечиваем правильное отображение фоновых изображений */
    background-position: center;
  }
  .button {
    margin-top: 14px;
    width: 296px;
    height: 32px;
    margin-bottom: 14px;
  }
  .button:hover {
    transform: none;
  }
  .button--wide {
    margin-left: -40px;
  }
  .button.button--wide.button--wide--mobile {
    margin-left: -60px;
  }
  .button.button--wide.button--wide--full {
    margin-left: -48px;
  }
  .button.button--modal {
    position: absolute;
    width: 106px;
    margin: 0;
  }
  .modal__content {
    width: 100%;
  }
  .modal__close {
    right: 0;
    color: black;
  }
  .modal__input {
    width: 315px;
  }
  .modal__title {
    font-size: 15px;
  }
  .modal__form {
    margin-top: -3px;
  }
  .product-card--standard {
    display: none;
  }
  .product-card--full {
    display: block !important;
    width: 328px;
    height: 328px;
    margin: 0;
  }
  .product-card--full:hover {
    box-shadow: none;
    transform: none;
  }
  .product-card--wide {
    width: 328px;
    height: 175px;
  }
  .product-card__labels--wide {
    position: absolute;
    top: 53px;
    left: 12px;
    display: flex;
  }
  .product-card__image-wrapper:hover {
    height: 328px;
  }
  .product-card__image-wrapper--wide-one {
    height: 175px;
  }
  .product-card__image-wrapper--wide-two {
    height: 175px;
  }
  .product-card__price.product-card__price--wide-two {
    margin: 0;
    margin-left: 70px;
    margin-bottom: -8px;
  }
  .product-card__price.product-card__price--wide-two.product-card__price--wide-two--mobile {
    margin-left: 85px;
  }
  .product-card__price-container {
    display: flex;
    position: absolute;
    left: 20%;
    bottom: 1px;
    flex-direction: column;
  }
  .products-slider {
    display: block;
    overflow: hidden;
    max-width: 100%;
    padding: 10px 0;
  }
  .products-slider__container {
    display: flex;
    transition: transform 0.3s ease;
    /* Плавная анимация для слайда */
    will-change: transform;
  }
  /* Стили для карточек с модификатором --slider */
  .product-card--slider {
    display: block;
    min-width: 250px;
    height: 410px;
    margin-right: 16px;
    /* Отступ между карточками */
    background-color: #fff;
    border-radius: 6px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    padding: 10px;
    transition: opacity 0.3s ease;
    opacity: 1;
    padding: 0;
    margin: 0;
    margin-left: 28px;
  }
  .product-card--slider.inactive {
    opacity: 0.5;
  }
  .product-card__image-wrapper--slider {
    position: relative;
    overflow: hidden;
  }
  .product-card__image--slider {
    width: 100%;
    height: 230px;
  }
  .product-card__labels--slider {
    position: absolute;
    top: 10px;
    left: 10px;
    display: flex;
    flex-direction: column;
  }
  .product-card__label--slider {
    background-color: #ff6a00;
    color: #fff;
    font-size: 12px;
    padding: 3px 6px;
    border-radius: 5px;
    margin-bottom: 5px;
  }
  .product-card__label--discount {
    height: 20px;
    width: 65px;
    padding-top: 7px;
  }
  .product-card__labels--wide.product-card__labels--wide--full {
    top: 60px;
  }
  .product-card__label--discount.product-card__label--discount-wide {
    margin-right: 4px;
  }
  .product-card__label--discount.product-card__label--discount-full {
    margin-right: 4px;
  }
  .product-card__label--day.product-card__label--day {
    width: 90px;
    margin-top: -3px;
  }
  .product-card__label--sale.product-card__label--sale-wide {
    width: 104px;
    margin-top: -1px;
  }
  .icon-container.icon-container--mobile {
    right: 70px;
  }
  .product-card__title--slider {
    width: 200px;
    height: 25px;
    font-size: 16px;
    margin: 0;
    margin-left: 14px;
    margin-top: 15px;
  }
  .product-card__price-container--slider {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0;
    margin-top: 38px;
  }
  .product-card__price--slider {
    font-size: 18px;
    font-weight: bold;
    margin: 0;
    margin-bottom: 10px;
  }
  .button--slider {
    width: 200px;
    height: 40px;
    margin: 0;
    background-color: #18c576;
    color: white;
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  .icon-container--slider {
    position: absolute;
    right: 10px;
    display: flex;
    font-size: 20px;
    color: white;
  }
  .slider-dots {
    display: flex;
    justify-content: center;
    margin-top: 10px;
  }
  .slider-dot {
    width: 10px;
    height: 10px;
    margin: 0 5px;
    background-color: #ccc;
    border-radius: 50%;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  .slider-dot.active {
    background-color: #000;
  }
  .product-card__label.product-card__label--day.product-card__label--slider {
    width: 103px;
    height: 23px;
    border-radius: 63px;
    padding-top: 8px;
    font-size: 15px;
  }
  .product-card__label.product-card__label--discount.product-card__label--discount-standard.product-card__label--slider {
    height: 18px;
    width: 51px;
    margin-left: 4px;
    border-radius: 50px;
    padding-top: 5px;
  }
}